import { Injectable } from '@angular/core';

import { BhLogger } from '../bh-common/bh-logger.service';
import { BhCanvasRect, BhCanvasRectPoint } from './bh-canvas-rect.interface';

@Injectable({
  providedIn: 'root'
})
export class BhCanvasRectService {

  constructor(private bhLogger: BhLogger) { }

  public rectToRectPoint(input: BhCanvasRect): BhCanvasRectPoint {
    if (!input) {
      this.bhLogger.warn(`Rect is empty.`, null, 'BhCanvasRectService', 'rectToRectPoint');
      return null;
    }

    let rectPoint: BhCanvasRectPoint = {
      leftBottom: {
        x: input.x,
        y: input.y + input.height
      },
      rightTop: {
        x: input.x + input.width,
        y: input.y
      }
    };

    return rectPoint;
  }

  public rectPointToRect(input: BhCanvasRectPoint): BhCanvasRect {
    if (!input) {
      this.bhLogger.warn(`Rect Point is empty.`, null, 'BhCanvasRectService', 'rectPointToRect');
      return null;
    }

    let BhCanvasRect: BhCanvasRect = {
      x: input.leftBottom.x,
      y: input.rightTop.y,
      width: input.rightTop.x - input.leftBottom.x,
      height: input.leftBottom.y - input.rightTop.y
    };

    return BhCanvasRect;
  }

  public getCoverageRect(mainRect: BhCanvasRect, subRect: BhCanvasRect): BhCanvasRect {
    let mainRectPoint: BhCanvasRectPoint = this.rectToRectPoint(mainRect);
    let subRectPoint: BhCanvasRectPoint = this.rectToRectPoint(subRect);
    
    var coverageRectPoint: BhCanvasRectPoint = {
      leftBottom: {
        x: Math.max(mainRectPoint.leftBottom.x, subRectPoint.leftBottom.x),
        y: Math.min(mainRectPoint.leftBottom.y, subRectPoint.leftBottom.y)
      },
      rightTop: {
        x: Math.min(mainRectPoint.rightTop.x, subRectPoint.rightTop.x),
        y: Math.max(mainRectPoint.rightTop.y, subRectPoint.rightTop.y)
      }
    };
    
    if (coverageRectPoint.leftBottom.x > coverageRectPoint.rightTop.x) {
      return null;
    } else if (coverageRectPoint.rightTop.y > coverageRectPoint.leftBottom.y) {
      return null;
    } else {
      return this.rectPointToRect(coverageRectPoint);
    }
  }

  public getPercentageOfCoverageByCoverageRect(mainRect: BhCanvasRect, coverageRect: BhCanvasRect): number {
    if (!coverageRect) {
      return 0;
    }
    
    return Math.ceil((coverageRect.width * coverageRect.height) / (mainRect.width * mainRect.height) * 100);
  }

  public getPercentageOfCoverage(mainRect: BhCanvasRect, subRect: BhCanvasRect): number {
    let coverageRect: BhCanvasRect = this.getCoverageRect(mainRect, subRect);
    
    if (!coverageRect) {
      return 0;
    }
    
    return this.getPercentageOfCoverageByCoverageRect(mainRect, coverageRect);
  }

}
