import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';

import { BhFormatService } from '../bh-common/bh-format.service';
import { BhHttpParams } from './bh-http.interface';

@Injectable({
  providedIn: 'root'
})
export class BhHttpService {

  constructor(
    private bhFormatService: BhFormatService
  ) { }

  public paramsToQueryString(params: BhHttpParams): string {
    let queryStringArray: Array<string> = [];

    for (let k in params) {
      let value: string = this.bhFormatService.toString(params[k]);

      if (value) {
        value = encodeURIComponent(value.toString());
      } else {
        value = '';
      }

      queryStringArray.push(`${k}=${value}`);
    }

    return queryStringArray.join('&');
  }

  public paramsToHttpParams(params: BhHttpParams): HttpParams {
    let httpParams: HttpParams = new HttpParams();

    for (let k in params) {
      httpParams = httpParams.set(k, this.bhFormatService.toString(params[k]));
    }

    return httpParams;
  }
}
