import { Component, AfterViewInit, ViewChild, Input, Output, ElementRef, EventEmitter } from '@angular/core';

import { BhLogger } from '../../bh-common/bh-logger.service';

@Component({
  selector: 'bh-user-camera',
  templateUrl: './user-camera.component.html',
  styleUrls: ['./user-camera.component.css']
})
export class UserCameraComponent implements AfterViewInit {

  @ViewChild('userCamera') videoElement: ElementRef;

  @Input() width: number = 640;

  @Input() height: number = 480;

  @Input() deviceId: string;

  @Input() facingMode: string;

  @Output() onReady: EventEmitter<HTMLMediaElement> = new EventEmitter();

  @Output() onError: EventEmitter<any> = new EventEmitter();

  private video: HTMLMediaElement;

  constructor(
    public bhLogger: BhLogger
  ) {}

  ngAfterViewInit(): void {
    this.video = this.videoElement.nativeElement;

    if (!this.facingMode || this.facingMode !== 'environment') {
      this.facingMode = 'user';
    }

    window.navigator.mediaDevices.getUserMedia({
      video: {
        width: {
          exact: this.width
        },
        height: {
          exact: this.height
        },
        deviceId: this.deviceId,
        facingMode: this.facingMode
      },
      audio: false
    }).then((stream: MediaStream) => {
      this.bhLogger.log(`User camera is ready.`, null, 'UserCameraComponent', 'ngAfterViewInit');
      this.video.srcObject = stream;
      this.onReady.emit(this.video);
    }).catch((error: any) => {
      this.bhLogger.warn(`Unable to get camera stream.`, error, 'UserCameraComponent', 'ngAfterViewInit');
      this.onError.emit(error);
    });
  }

}
