import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BhLogger {

  constructor() { }

  public toLogText(logType: string, logMsg: string, logContext?: {[key: string]: any}, logModule?: string, logFunc?: string) {
    var LogText: Array<string> = [];
      
    LogText.push(`[${logType}]`);

    if (logModule) {
      if (logFunc) {
        LogText.push(`[${logModule}::${logFunc}]`);
      } else {
        LogText.push(`[${logModule}]`);
      }
    }

    LogText.push(` ${logMsg}`);

    if (logContext) {
      for (let k in logContext) {
        if (logContext[k] && logContext[k] instanceof Error) {
          logContext[k] = logContext[k].message;
        }
      }
      
      LogText.push(` [context: ${JSON.stringify(logContext)}]`);
    }

    return LogText.join('');
  }

  public log(logMsg: string, logContext?: object, logModule?: string, logFunc?: string): void {
    console.log(this.toLogText('LOG', logMsg, logContext, logModule, logFunc));
  }

  public debug(logMsg: string, logContext?: object, logModule?: string, logFunc?: string): void {
    console.trace(this.toLogText('DEBUG', logMsg, logContext, logModule, logFunc));
  }

  public info(logMsg: string, logContext?: object, logModule?: string, logFunc?: string): void {
    console.info(this.toLogText('INFO', logMsg, logContext, logModule, logFunc));
  }

  public warn(logMsg: string, logContext?: object, logModule?: string, logFunc?: string): void {
    console.warn(this.toLogText('WARN', logMsg, logContext, logModule, logFunc));
  }

  public error(logMsg: string, logContext?: object, logModule?: string, logFunc?: string): void {
    console.error(this.toLogText('ERROR', logMsg, logContext, logModule, logFunc));
  }
  
}
