import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BhFormatService {

  constructor() { }

  public toString(input: any, defaultValue?: string): string {
    if (typeof input === 'boolean') {
      return input.toString();
    }

    return input ? input.toString() : (defaultValue ? defaultValue : '');
  }

  public toNumber(input: any, defaultValue?: number): number {
    if (typeof input === 'string' && !/^[0-9\-\.]*$/g.test(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    if (Array.isArray(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    input = parseInt(input, 10);

    if (isNaN(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    return input;
  }

  public toFloat(input: any, defaultValue?: number): number {
    if (typeof input === 'string' && !/^[0-9\-\.]*$/g.test(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    if (Array.isArray(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    input = parseFloat(input);

    if (isNaN(input)) {
      return (defaultValue ? defaultValue : 0);
    }

    return input;
  }

  public toBoolean(input: any, defaultValue?: boolean): boolean {
    if (typeof input === 'string') {
      if (input === 'true') {
        return true;
      } else if (input === 'false') {
        return false;
      }
    }

    if (typeof input !== 'boolean') {
      return (defaultValue ? defaultValue : false);
    }

    return input ? true : false;
  }

  public toArrayChunk(input: Array<any>, chunkSize: number): Array<Array<any>> {
    if (!input || input.length <= 0) {
      return [[]];
    }

    chunkSize = this.toNumber(chunkSize);

    if (chunkSize <= 0) {
      return [input];
    }

    let results = [];
    let inputClone: Array<any> = input.slice(0);
    
    while (inputClone.length) {
      results.push(inputClone.splice(0, chunkSize));
    }
    
    return results;
  }

  public tsToDate(input: any): Date {
    input = this.toNumber(input);
    return new Date(input * 1000);
  }

  public dateToTs(input: Date): number {
    if (!input) {
      input = new Date();
    }

    return Math.floor(input.getTime() / 1000);
  }

}
